.notfound {
    .content {
        width: 50%;
        margin: auto;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-right: -50%;
        transform: translate(-50%, -50%);

        h2 {
            text-align: center;
        }

        p {
            text-align: center;
        }
    }
}
